.location-box {
    
    .location{
        color: #FFF;
        font-size: 32px;
        font-weight: 500;
        text-align: center;
        text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    }

    .date{
        color: #FFF;
        font-size: 20px;
        font-weight: 300;
        font-style: italic;
        text-align: center;
        text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
    }
}

.weather-box{
    text-align: center;

    .temp{
        position: relative;
        display: inline-block;
        margin: 30px auto;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 16px;
      
        padding: 15px 25px;
      
        color: #FFF;
        font-size: 102px;
        font-weight: 900;
      
        text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
        text-align: center;
        box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    }

    .weather{
        color: #FFF;
        font-size: 48px;
        font-weight: 700;
        text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    }
}